.header {
  background: #fff url("http://wp-shop/wp-content/uploads/2020/03/low-angle-shot-of-a-person-climbing-the-snow.jpg") center 0px no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3); } }
.nav-bar {
  position: relative;
  &-wrap {
    display: flex;
    justify-content: space-between; }
  &__logo {
    margin-top: 30px; }
  &__menu {
    display: flex;
    justify-content: space-between;
    align-content: center; }
  &__menu-container {
    display: flex;
    li {
      margin: 40px 25px 40px 25px;
      list-style: none;
      a {
        color: #fff;
        text-decoration: none; } } }
  &__menu-basket {
    margin-top: 40px; } }

.hero {
  padding-top: 230px;
  padding-bottom: 270px;
  position: relative;
  &__header {
    color: $f-white;
    margin: 0 auto;
    display: block;
    font-size: 60px;
    text-align: center; }
  &__text {
    color: $f-white;
    margin: 20px auto 0px auto;
    display: block;
    font-size: 20px;
    text-align: center;
    max-width: 600px; } }
.arrow {
  position: relative;
  text-align: center;
  padding-bottom: 50px;
  &__img {
    width: 36px;
    height: 25px; } }

.header-page {
  background: #aa9b77; }

